  --ring: #007acc;
}
@layer base {
  * {
    border-color: var(--border);
    box-sizing: border-box;
  }
  html {
    background-color: var(--background);
    color: var(--foreground);
    font-family: 'Cascadia Code', 'Fira Code', 'SF Mono', Monaco, 'Roboto Mono', Consolas, 'Liberation Mono', 'Courier New', monospace;
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
    font-family: 'Cascadia Code', 'Fira Code', 'SF Mono', Monaco, 'Roboto Mono', Consolas, 'Liberation Mono', 'Courier New', monospace;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: 'Cascadia Code', 'Fira Code', 'SF Mono', Monaco, 'Roboto Mono', Consolas, 'Liberation Mono', 'Courier New', monospace;
    font-weight: 700;
  }
}
@keyframes fade-in {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-in {
  animation: fade-in 0.5s ease-out;
}
.animate-fade-in-up {
  animation: fade-in-up 0.6s ease-out;
}
.hover\:scale-102:hover {
  transform: scale(1.02);
}
.vscode-scrollbar::-webkit-scrollbar {
  width: 14px;
  height: 14px;
}
.vscode-scrollbar::-webkit-scrollbar-track {
  background: var(--background);
}
.vscode-scrollbar::-webkit-scrollbar-thumb {
  background: #424242;
  border-radius: 0;
  border: 3px solid var(--background);
}
.vscode-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #4f4f4f;
}
.vscode-scrollbar::-webkit-scrollbar-corner {
  background: var(--background);
}
.vscode-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #424242 var(--background);
